/* Navbar.css */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #2c3e50; /* Dark blue background */
    padding: 1rem 2rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Subtle shadow */
  }
  
  .navbar-logo {
    font-size: 1.5rem;
    font-weight: bold;
    color: #ecf0f1; /* Light gray text */
    text-transform: uppercase;
    letter-spacing: 2px;
  }
  
  .navbar-links {
    display: flex;
    gap: 1.5rem;
  }
  
  .navbar-links a {
    color: #ecf0f1; /* Light gray text */
    text-decoration: none;
    padding: 0.5rem 1rem;
    font-size: 1rem;
    transition: all 0.3s ease; /* Smooth transition */
    border-radius: 4px;
  }
  
  .navbar-links a:hover {
    background-color: #3498db; /* Bright blue on hover */
    color: #fff; /* White text on hover */
    transform: translateY(-2px); /* Slight lift effect */
  }
  
  .navbar-toggle {
    display: none;
    flex-direction: column;
    cursor: pointer;
  }
  
  .navbar-toggle .bar {
    width: 25px;
    height: 3px;
    background-color: #ecf0f1; /* Light gray bars */
    margin: 4px 0;
    transition: all 0.3s ease; /* Smooth transition */
  }
  
  /* Animation for the toggle button when active */
  .navbar-toggle.active .bar:nth-child(1) {
    transform: rotate(45deg) translate(5px, 5px);
  }
  
  .navbar-toggle.active .bar:nth-child(2) {
    opacity: 0;
  }
  
  .navbar-toggle.active .bar:nth-child(3) {
    transform: rotate(-45deg) translate(5px, -5px);
  }
  
  /* Responsive styles */
  @media (max-width: 768px) {
    .navbar {
      padding: 1rem;
    }
  
    .navbar-links {
      display: none;
      flex-direction: column;
      width: 100%;
      background-color: #34495e; /* Slightly darker background for mobile */
      position: absolute;
      top: 60px; /* Adjust based on navbar height */
      left: 0;
      padding: 1rem 0;
      box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    }
  
    .navbar-links.active {
      display: flex;
    }
  
    .navbar-toggle {
      display: flex;
    }
  }
  
  /* Additional hover effects for the logo */
  .navbar-logo:hover {
    color: #3498db; /* Bright blue on hover */
    cursor: pointer;
  }
  
  /* Optional: Add a gradient background to the navbar */
  .navbar {
    background: linear-gradient(135deg, #2c3e50, #34495e);
  }