.skills {
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.skills h2 {
  text-align: center;
  margin-bottom: 30px;
  font-size: 24px;
  color: #333;
}

.skills-container {
  display: flex;
  gap: 20px; 
 
}

.skills-category {
  flex: 1; /* Each category takes equal width */
  background-color: #fff;
  padding: 20px;
  border: solid 1px rgba(0, 0, 0, 0.1);
  border-color: #6a11cb;
  border-radius: 8px;


  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.skills-category h3 {
  margin-bottom: 20px;
  font-size: 20px;
  color: #555;
}

.skill-item {
  margin-bottom: 15px;
}

.skill-name {
  display: block;
  margin-bottom: 5px;
  font-size: 16px;
  color: #333;
}

.skill-progress {
  width: 100%;
  height: 10px;
  background-color: #e0e0e0;
  border-radius: 5px;
  overflow: hidden;
}

.progress-bar {
  height: 100%;
  background-color: #007bff;
  border-radius: 5px;
  transition: width 0.3s ease;
}

.skill-percentage {
  display: block;
  margin-top: 5px;
  font-size: 14px;
  color: #777;
}