/* ContactsData.css */
.contact {
  max-width: 100%;
  margin: 0 auto;
  padding: 2rem;
  background: #0e0d0d;
  font-family: 'Poppins', sans-serif;
  color: #333;
}

.contact h2 {
  font-size: 2rem;
  color: #2c3e50; /* Dark blue for heading */
  margin-bottom: 1.5rem;
  text-align: center;
}

.contact-container {
  display: flex;
  gap: 2rem;
  align-items: flex-start;
}

.contact-details {
  flex: 1;
  background: #1a1919;
 
  padding: 1.5rem;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.contact-details h3 {
  font-size: 1.5rem;
  color: #2575fc; /* Blue for subheading */
  margin-bottom: 1rem;
}

.contact-details p {
  font-size: 1rem;
  color: wheat;
  margin-bottom: 1.5rem;
}

.contact-details ul {
  list-style-type: none;
  padding: 0;
}

.contact-details ul li {
  font-size: 1rem;
  color: wheat;
  margin-bottom: 0.75rem;
}

.contact-details ul li span {
  margin-right: 0.5rem;
}

.contact-form {
  flex: 1;
 background: black;
 color: wheat;
  padding: 1.5rem;
   border: solid 1px rgba(0, 0, 0, 0.1);
  border-color: #6a11cb;
  border-radius: 8px;
 
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.form-group label {
  font-size: 1rem;
  color: wheat;}

.form-group input,
.form-group textarea {
  padding: 0.75rem;
  border: 1px solid #0e0d0d; /* Light gray border */
  border-radius: 6px;
  font-size: 1rem;
  color: white;
  background: #0e0d0d;
}

.form-group textarea {
  resize: vertical;
  min-height: 150px;
}

.submit-button {
 width: 100%;
  padding: 0.75rem 1.5rem;
   background: linear-gradient(135deg, #6a11cb, #2575fc);
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.3s ease;
}

.submit-button:hover {
  background: #1a5bbf; /* Darker blue on hover */
}

/* Responsive Design */
@media (max-width: 768px) {
  .contact-container {
    flex-direction: column;
  }

  .contact-details,
  .contact-form {
    width: 100%;
  }
}