.home-container {
  text-align: center;
  padding: 20px;
}

.hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 50px 0;
}

.hero-content {
  max-width: 600px;
  text-align: left;
}

.hero-content h1 {
  font-size: 3rem;
  margin-bottom: 20px;
}

.hero-content p {
  font-size: 1.2rem;
  margin-bottom: 30px;
}

.cta-button {
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  border-radius: 5px;
  text-decoration: none;
  font-size: 1rem;
}

.profile-image {
  width: 300px;
  height: 300px;
  border-radius: 50%;
  background: rgb(99, 92, 92);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.resume-section {
  margin-top: 50px;
}

.download-button {
  background-color: #28a745;
  color: white;
  padding: 10px 20px;
  border-radius: 5px;
  text-decoration: none;
  font-size: 1rem;
}