@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

.projects {
  text-align: center;
  padding: 4rem 2rem;
  background: #121212;
  font-family: 'Poppins', sans-serif;
}

.projects h1 {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 2rem;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: 2px 2px 10px rgba(0, 0, 0, 0.3);
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  justify-content: center;
  padding: 2rem;
}

.project-card {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  backdrop-filter: blur(8px);
  position: relative;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.project-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.5);
}

.project-image {
  width: 100%;
  height: 180px;
  object-fit: cover;
  border-radius: 10px;
}

.project-card h2 {
  font-size: 1.5rem;
  font-weight: 600;
  color: #fff;
  margin-top: 1rem;
  text-align: center;
}

.project-card p {
  font-size: 1rem;
  color: #d0d0d0;
  line-height: 1.6;
  margin-top: 0.5rem;
  text-align: justify;
  max-height: 80px;
  overflow: hidden;
  transition: max-height 0.3s ease;
}

.project-card.expanded p {
  max-height: 500px;
}

.read-more {
  cursor: pointer;
  color: #6a11cb;
  font-weight: 500;
  margin-top: 0.5rem;
  text-decoration: underline;
  transition: color 0.3s ease;
}

.read-more:hover {
  color: #2575fc;
}

.tags {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-top: 1rem;
}

.tag {
  background: linear-gradient(135deg, #6a11cb, #2575fc);
  color: #fff;
  padding: 5px 12px;
  border-radius: 8px;
  font-size: 0.9rem;
  font-weight: 500;
}

.view-button {
  margin-top: 1rem;
  background: linear-gradient(135deg, #6a11cb, #2575fc);
  color: white;
  padding: 10px 20px;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 600;
  text-transform: uppercase;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.view-button:hover {
  transform: scale(1.05);
  box-shadow: 0 5px 15px rgba(106, 17, 203, 0.5);
}

/* Responsive */
@media (max-width: 768px) {
  .projects h1 {
    font-size: 2rem;
  }

  .project-card {
    padding: 1rem;
  }

  .project-card h2 {
    font-size: 1.2rem;
  }

  .project-card p {
    font-size: 0.9rem;
  }

  .view-button {
    font-size: 0.9rem;
  }
}
